<template>
  <loading-component :active="isLoading"></loading-component>
  <div class="text-end m-2">
    <button class="btn btn-primary" type="button" @click="openModal(true)"></button>
  </div>
  <table class="table mt-4">
    <thead>
      <tr>
        <th></th>
        <th></th>
        <th></th>
        <th></th>
        <th></th>
        <th></th>
        <th></th>
      </tr>
    </thead>
    <tbody>
      <tr v-for="item in products" :key="item.id">
        <td>{{ item.category }}</td>
        <td> <img src="{{ item.imageUrl }}" alt="" ></td>
        <td>{{ item.title }}</td>
        <td>{{ item.origin_price }}</td>
        <td>{{ item.price }}</td>
        <td>
          <span class="text-success" v-if="item.is_enabled"><strong></strong></span>
          <span class="text-muted" v-else><strong></strong></span>
        </td>
        <td>
          <div class="btn-group">
            <button class="btn btn-primary"
            @click="openModal(false, item)"></button>
            <button class="btn btn-danger"
            @click="openModal(false, item,'delete')"></button>
          </div>
        </td>
      </tr>
    </tbody>
  </table>
  <pagination-bar :pages="pagination"
    @emit-pages="getProducts"></pagination-bar>
  <product-modal ref="productModal"
  :product="tempProduct"
  @update-product="updateProduct"></product-modal>
  <del-modal ref="delModal"
  :item="tempProduct"
  @delete-product="deleteProduct"></del-modal>
</template>

<script>
import ProductModal from '../components/ProductModal.vue'
import DelModal from '../components/DelModal.vue'
import PaginationBar from '../components/PaginationBar.vue'
export default {
  data () {
    return {
      products: [],
      pagination: {},
      tempProduct: {},
      isNew: false,
      isLoading: false
    }
  },
  components: {
    ProductModal,
    DelModal,
    PaginationBar
  },
  inject: ['emitter'],
  methods: {
    getProducts (page = 1) {
      const api = `${process.env.VUE_APP_API}api/${process.env.VUE_APP_PATH}/admin/products/?page=${page}`
      this.isLoading = true
      this.$http.get(api)
        .then((res) => {
          this.isLoading = false
          if (res.data.success) {
            this.products = res.data.products
            this.pagination = res.data.pagination
          }
        })
    },
    openModal (isNew, item, modal) {
      if (isNew) {
        this.tempProduct = {}
      } else {
        this.tempProduct = { ...item }
      }
      let productComponent = this.$refs.productModal
      if (modal === 'delete') {
        productComponent = this.$refs.delModal
      }
      productComponent.showModal()
      this.isNew = isNew
    },
    updateProduct (item) {
      this.tempProduct = item
      // 
      let api = `${process.env.VUE_APP_API}api/${process.env.VUE_APP_PATH}/admin/product`
      let httpMethod = 'post'
      // 
      if (!this.isNew) {
        api = `${process.env.VUE_APP_API}api/${process.env.VUE_APP_PATH}/admin/product/${item.id}`
        httpMethod = 'put'
      }
      const productComponent = this.$refs.productModal
      this.$http[httpMethod](api, { data: this.tempProduct })
        .then((res) => {
          if (res.data.success) {
            this.getProducts()
            this.emitter.emit('push-message', {
              style: 'success',
              title: ''
            })
          } else {
            this.emitter.emit('push-message', {
              style: 'danger',
              title: '',
              content: res.data.message.join('')
            })
          }
          console.log(res)
          productComponent.hideModal()
        })
    },
    deleteProduct (item) {
      this.tempProduct = item
      const api = `${process.env.VUE_APP_API}api/${process.env.VUE_APP_PATH}/admin/product/${item.id}`
      const productComponent = this.$refs.delModal
      this.$http.delete(api)
        .then((res) => {
          if (res.data.success) {
            console.log('')
            this.emitter.emit('push-message', {
              style: 'success',
              title: ''
            })
            productComponent.hideModal()
            this.getProducts()
          }
        })
    }
  },
  created () {
    this.getProducts()
  }
}
</script>
